/* open-sans-300 - latin_cyrillic */
@font-face {
     font-family: 'Open Sans';
     font-style: normal;
     font-weight: 300;
     src: url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300.eot'); /* IE9 Compat Modes */
     src: local('Open Sans Light'), local('OpenSans-Light'),
          url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
          url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300.woff2') format('woff2'), /* Super Modern Browsers */
          url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300.woff') format('woff'), /* Modern Browsers */
          url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300.ttf') format('truetype'), /* Safari, Android, iOS */
          url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300.svg#OpenSans') format('svg'); /* Legacy iOS */
   }
   /* open-sans-300italic - latin_cyrillic */
   @font-face {
     font-family: 'Open Sans';
     font-style: italic;
     font-weight: 300;
     src: url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300italic.eot'); /* IE9 Compat Modes */
     src: local('Open Sans Light Italic'), local('OpenSans-LightItalic'),
