          url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300italic.ttf') format('truetype'), /* Safari, Android, iOS */
          url('../fonts/OpenSans/open-sans-v15-latin_cyrillic-300italic.svg#OpenSans') format('svg'); /* Legacy iOS */
   }
